(function () {
  'use strict';

  angular.module('BlurAdmin.pages.profile')
    .controller('ProfilePageCtrl', ProfilePageCtrl);

  ProfilePageCtrl.$inject = ['$log', '$scope', '$rootScope', 'fileReader', '$filter', '$uibModal', 
    'localStorageService', 'Restangular', 'toastr', 'Upload'];
  function ProfilePageCtrl($log, $scope, $rootScope, fileReader, $filter, $uibModal,
    localStorageService, Restangular, toastr, Upload) {
    var vm = this;

    vm.user = localStorageService.get('user');
    vm.password2 = vm.user.password;

    // console.log(vm.user)

    vm.submit = function() {
      var user = {
        email: vm.user.email,
        name: vm.user.name,
        nombreCompleto: vm.user.nombreCompleto,
        password: vm.user.password,
        privilegio: vm.user.privilegio,
        imagen: vm.user.imagen
      }

      if (vm.user.password !== vm.password2) {
        toastr.error('Las contraseas no coinciden', 'Error');
        return;
      }

      Restangular.one('usuarios', vm.user.id).customPUT(vm.user).then(function (resp) {
        toastr.success('El perfil fue actualizado correctamente', 'Perfil');
        $rootScope.updateProfileImage(vm.user.imagen);
        localStorageService.set('user', vm.user);
      }, function () {
        toastr.error('Error al actualizando el perfil', 'Error');
      })
    };

    vm.updateProfile = function (files) {
      if (_.isObject(vm.user.imagen) && !vm.user.imagen.$error) {
        var timestamp = Number(new Date());
        var storageRef = firebase.storage().ref(timestamp.toString());
        var uploadTask = storageRef.put(vm.user.imagen);

        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
          function(snapshot) {
            // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
            var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            console.log('Upload is ' + progress + '% done');
            switch (snapshot.state) {
              case firebase.storage.TaskState.PAUSED: // or 'paused'
                console.log('Upload is paused');
                break;
              case firebase.storage.TaskState.RUNNING: // or 'running'
                console.log('Upload is running');
                break;
            }
          }, function(error) {
            // A full list of error codes is available at
            // https://firebase.google.com/docs/storage/web/handle-errors
            switch (error.code) {
              case 'storage/unauthorized':
                // User doesn't have permission to access the object
                $log.error('UNAUTHORIZED');
                break;

              case 'storage/canceled':
                // User canceled the upload
                $log.error('CANCELED');
                break;

              case 'storage/unknown':
                $log.error('UNKNOWN');
                // Unknown error occurred, inspect error.serverResponse
                break;
            }
          }, function() {
            // Upload completed successfully, now we can get the download URLSa
            vm.user.imagen = uploadTask.snapshot.downloadURL;
            vm.submit();
          });
      } else {
        vm.submit();
      }
    };

    vm.getProfileImage = function() {
      var defaultImage =  '/assets/img/app/profile/empty-profile.png';
      return vm.user.imagen ? _.isObject(vm.user.imagen) ? vm.user.imagen : 
        vm.user.imagen.startsWith('http') ? vm.user.imagen : defaultImage : defaultImage;
    }
  }
})();

/**
 * Change top "Daily Downloads", "Active Users" values with animation effect
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('animatedChange', animatedChange);

  /** @ngInject */
  function animatedChange($timeout) {
    return {
      link: function (scope, element) {
        $timeout(function () {
          var newValue = element.attr('new-value');
          var oldvalue = parseInt(element.html());

          function changeValue(val) {
            $timeout(function () {
              element.html(val);
            }, 30);
          }

          if (newValue > oldvalue) {
            for (var i = oldvalue; i <= newValue; i++) {
              changeValue(i);
            }
          } else {
            for (var j = oldvalue; j >= newValue; j--) {
              changeValue(j);
            }
          }
          $timeout(function () {
            element.next().find('i').addClass('show-arr');
          }, 500);
        }, 3500);
      }
    };
  }

})();
/**
 * Auto expand textarea field
 */
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoExpand', autoExpand);

  /** @ngInject */
  function autoExpand() {
    return {
      restrict: 'A',
      link: function ($scope, elem) {
        elem.bind('keydown', function ($event) {
          var element = $event.target;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        });

        // Expand the textarea as soon as it is added to the DOM
        setTimeout(function () {
          var element = elem;
          $(element).height(0);
          var height = $(element)[0].scrollHeight;
          height = (height < 16) ? 16 : height;
          $(element).height(height);
        }, 0)
      }
    };
  }

})();
(function () {
  'use strict';

  angular.module('BlurAdmin.theme')
      .directive('autoFocus', autoFocus);

  /** @ngInject */
  function autoFocus($timeout, $parse) {
    return {
      link: function (scope, element, attrs) {
        var model = $parse(attrs.autoFocus);
        scope.$watch(model, function (value) {
          if (value === true) {
            $timeout(function () {
              element[0].focus();
              element[0].select();
            });
          }
        });
        element.bind('blur', function () {
          scope.$apply(model.assign(scope, false));
        });
      }
    };
  }

})();
