(function() {
  'use strict';

  angular.module('BlurAdmin.pages.almacenes')
    .controller('almacenesRegistroController', almacenesRegistroController);

  almacenesRegistroController.$inject = ['$log', '$state', '$rootScope', '$scope', 'localStorageService', 'serverAPI', 'Restangular', 'Upload'];
  function almacenesRegistroController($log, $state, $rootScope, $scope, localStorageService, serverAPI, Restangular, Upload) {
    $log.log('productosRegistroController');
    var vm = this;
    vm.user = localStorageService.get('user');

    vm.propietarios = [];
    vm.almacen = {
      idPropietario: 0,
      aNombre: '',
      aImagen: '',
      aDireccion: '',
      aUbicacion: '',
      aHorario: '',
      aEntrega: 'recoge'
    };

    $scope.$on('mapInitialized', function(evt, evtMap) {
      vm.map = evtMap;
    });

    vm.submit = function() {
      var currentPosition = vm.map.markers[0].getPosition();
      vm.almacen.aUbicacion = '[' + currentPosition.lat() + ',' + currentPosition.lng() + ']';
      vm.almacen.idPropietario = vm.user.privilegio !== 'administrador' ?
        vm.user.propietario.id : vm.almacen.idPropietario;

      serverAPI.almacenes.post(vm.almacen).then(function(resp) {
        $state.go('almacenes.listar');
      });
    };

    vm.upload = function(files) {
      if (!vm.file.$error) {
        if (_.isObject(vm.file) && !vm.file.$error) {
          var timestamp = Number(new Date());
          var storageRef = firebase.storage().ref(timestamp.toString());
          var uploadTask = storageRef.put(vm.file);

          uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
            function(snapshot) {
              var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
              console.log('Upload is ' + progress + '% done');
              switch (snapshot.state) {
                case firebase.storage.TaskState.PAUSED:
                  console.log('Upload is paused');
                  break;
                case firebase.storage.TaskState.RUNNING:
                  console.log('Upload is running');
                  break;
              }
            }, function(error) {
              $log.error(error.code);
            }, function() {
              vm.almacen.aImagen = uploadTask.snapshot.downloadURL;
              vm.submit();
            });
        } else {
          vm.submit();
        }
      }
    };

    Restangular.all('propietarios').getList().then(function(resp) {
      vm.propietarios = (resp || []);
    });
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.almacenes')
    .controller('vendedoresAlmacenListaController', vendedoresAlmacenListaController);

  vendedoresAlmacenListaController.$inject = ['$log', '$state', '$stateParams', '$uibModal', 'serverAPI', 'Restangular', 'localStorageService'];
  function vendedoresAlmacenListaController($log, $state, $stateParams, $uibModal, serverAPI, Restangular, localStorageService) {
    $log.log('vendedoresAlmacenListaController');
    var vm = this;
    vm.user = localStorageService.get('user') || {};
    vm.vendedores = [];
    vm.busqueda = {nombre: ''};

    vm.goTo = function(id) {
      $state.go('vendedores.item', {id: id});
    }

    vm.remove = function($index, id) {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/vendedores/borrar/borrar.template.html',
        controller: 'vendedoresBorrarController',
        controllerAs: 'vm',
        resolve: {}
      });

      deleteMessage.result.then(function() {
        Restangular.one('vendedores', id).remove().then(function() {
          vm.vendedores.splice($index, 1);
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.buscar = function() {
      var id = vm.user.privilegio === 'vendedor' ? vm.user.vendedor.idAlmacen : $stateParams.id;
      Restangular.one('almacenes', id).getList('vendedores').then(function(resp) {
        vm.vendedores = resp || [];
      });
    };

    vm.user = function(idUser) {
      $state.go('user', {id: idUser});
    };

    vm.addVendedor = function() {
      $state.go('vendedores_registrar', {id: $stateParams.id});
    };

    vm.buscar();
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.almacenes')
    .controller('almacenesListaController', almacenesListaController);

  almacenesListaController.$inject = ['$log', '$state', '$uibModal', '$rootScope', 'localStorageService', 'serverAPI', 'Restangular'];
  function almacenesListaController($log, $state, $uibModal, $rootScope, localStorageService, serverAPI, Restangular) {
    $log.log('almacenesListaController');
    var vm = this;
    vm.user = localStorageService.get('user');
    vm.almacenes = [];
    vm.busqueda = {nombre: ''};
    vm.isLoading = false;

    vm.productos = [];
    vm.positions = [];
    vm.busqueda = {nombre: ''};
    vm.maxSize = 5;
    vm.itemsPerPage = 10;
    vm.total = 0;
    vm.page = 1;
    vm.hayFerreterias = false;

    vm.goTo = function(id) {
      $state.go('almacenes.item', {id: id});
    };

    vm.goToProductos = function(id) {
      $state.go('categories', {id: id});
    };

    vm.goToVendedores = function(id) {
      $state.go('almacenes_item_vendedores', {id: id})
    };

    vm.remove = function($index, id) {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/almacenes/borrar/borrar.template.html',
        controller: 'almacenesBorrarController',
        controllerAs: 'vm',
        resolve: {}
      });

      deleteMessage.result.then(function() {
        Restangular.one('almacenes', id).remove().then(function() {
          vm.almacenes.splice($index, 1);
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.buscar = function() {
      vm.isLoading = false;
      vm.busqueda.page = vm.page;
      vm.busqueda.limit = vm.itemsPerPage;

      if (vm.user.privilegio !== 'administrador') {
        var id = (vm.user.propietario || vm.user.vendedor).id;
        Restangular.one('propietarios', id).customGET('almacenes', vm.busqueda).then(function(resp) {
          vm.almacenes = ((resp || {}).almacenes || []).map(function(almacen) {
            return almacen;
          });

          vm.hayFerreterias = true;
          vm.isLoading = true;
        });
      } else {
        Restangular.all('almacenes').customGET('', vm.busqueda).then(function(resp) {
          vm.almacenes = (resp.data || []);

          vm.total = (resp || {}).total || 0;
          vm.page = (resp || {}).current_page || 0;
          vm.hayFerreterias = true;
          vm.isLoading = true;
        })
      }
    };

    vm.buscar();
  }
})();

(function() {
  'use strict';

  angular.module('BlurAdmin.pages.almacenes')
    .controller('almacenesItemController', almacenesItemController);

  almacenesItemController.$inject = ['$log', '$scope', '$state', '$stateParams', '$uibModal', '$rootScope',
    'Restangular', 'serverAPI', 'toastr'];
  function almacenesItemController($log, $scope, $state, $stateParams, $uibModal, $rootScope, Restangular,
                                   serverAPI, toastr) {
    $log.log('almacenesItemController');
    var vm = this;
    vm.almacen = {};

    $scope.$on('mapInitialized', function(evt, evtMap) {
      vm.map = evtMap;
    });

    vm.remove = function() {
      var deleteMessage = $uibModal.open({
        animation: true,
        templateUrl: 'app/pages/almacenes/borrar/borrar.template.html',
        controller: 'almacenesBorrarController',
        controllerAs: 'vm',
        resolve: {}
      });

      deleteMessage.result.then(function() {
        Restangular.one('almacenes', vm.almacen.id).remove().then(function() {
          $state.go('almacenes');
        });
      }, function() {
        $log.log('Borrar fue cancelado')
      });
    };

    vm.submit = function() {
      var currentPosition = vm.map.markers[0].getPosition();
      vm.almacen.aUbicacion = '[' + currentPosition.lat() + ',' + currentPosition.lng() + ']';

      Restangular.one('almacenes', vm.almacen.id).customPUT(vm.almacen).then(function(resp) {
        toastr.success('Datos actualizados correctamente', 'Actualizado');
        $state.go('almacenes.listar');
      }, function() {
        toastr.error('Error actualizando Ferreteria');
      });
    };

    vm.upload = function(files) {
      if (_.isObject(vm.file) && !vm.file.$error) {
        var timestamp = Number(new Date());
        var storageRef = firebase.storage().ref(timestamp.toString());
        var uploadTask = storageRef.put(vm.file);

        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
          function(snapshot) {
            var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
            console.log('Upload is ' + progress + '% done');
            switch (snapshot.state) {
              case firebase.storage.TaskState.PAUSED:
                console.log('Upload is paused');
                break;
              case firebase.storage.TaskState.RUNNING:
                console.log('Upload is running');
                break;
            }
          }, function(error) {
            $log.error(error.code);
          }, function() {
            vm.almacen.aImagen = uploadTask.snapshot.downloadURL;
            vm.submit();
          });
      } else {
        vm.submit();
      }
    };

    serverAPI.almacenes.get($stateParams.id).then(function(resp) {
      vm.almacen = {
        id: resp.id,
        aNombre: resp.aNombre || '',
        aImagen: resp.aImagen || '/assets/pictures/empty.png',
        aDireccion: resp.aDireccion || '',
        aUbicacion: resp.aUbicacion || 'current-location',
        aHorario: resp.aHorario
      };
    })
  }
})();

angular
  .module('BlurAdmin.pages.buscador')
  .controller('MapaBusquedaController', MapaBusquedaController);

MapaBusquedaController.$inject = [
  '$rootScope',
  '$state',
  '$timeout',
  'ngDialog',
  'NgMap',
  'positions',
  'productos'
];

function MapaBusquedaController($rootScope, $state, $timeout, ngDialog, NgMap, almacenes, productos) {
  var vm = this;
  vm.almacenes = almacenes;
  vm.productos = productos;

  vm.almacenes = vm.almacenes.map(function(almacen) {
    almacen.imagen = almacen.imagen.indexOf('http://') >= 0 || almacen.imagen.indexOf('https://') >= 0 ?
      almacen.imagen : $rootScope.baseURL + 'api/fileentry/get/' + almacen.imagen;
    return almacen;
  })

  vm.initializeMap = function(map) {
    vm.map = map;
    
    _.each((map || {}).markers || [], function(marker, index) {
      marker.addListener('click', vm.openFerreteria(index, marker));
    });
  };

  vm.goToFerreteria = function(idFerreteria) {
    $state.go('almacenes.item', {id: idFerreteria})
  };
  
  window.closeMap = function() {
    ngDialog.closeAll();
  };

  vm.openFerreteria = function(index, marker) {
    return function() {
      var almacen = vm.almacenes[index];
      var infoWindow = new google.maps.InfoWindow({
        content: [
          '<div class="item-producto-mapa">',
            '<img class="imagen-producto" src="', almacen.imagen,'">',
            '<div class="descripcion">',
              '<h2>', almacen.nombre, '<h2>',
              '<p>', almacen.direccion, '</p>',
            '</div>',
          '</div>',
          '<a href="/#/almacenes/', almacen.id ,'" onclick="closeMap()">',
            '<i class="fa fa-industry"></i>Ir a Ferreteria',
          '</a>',
        ].join('')
      });

      infoWindow.open(vm.map, marker);
    };
  };

  $timeout(function() {
    NgMap.initMap('mapBusqueda');
  }, 300);
}

(function() {
  angular
    .module('BlurAdmin.pages.dashboard')
    .controller('dashboardProductosController', dashboardProductosController);

  dashboardProductosController.$inject = [
    '$element',
    'baConfig',
    'layoutPaths',
    'Restangular'
  ];

  function dashboardProductosController($element, baConfig, layoutPaths, Restangular) {
    var vm = this;

    vm.isLoading = true;
    vm.haveResults = false;
    vm.format = 'yyyy-MM-dd';
    vm.productos = [];
    vm.options = {
      showWeeks: false
    };

    vm.open = function(control) {
      vm[control + 'Opened'] = true;
    };

    vm.generateReport = function() {
      var layoutColors = baConfig.colors;
      var id = 'pieChart';

      vm.haveResults = false;
      vm.isLoading = false;

      var startDate = vm.startDate ? moment(vm.startDate).format('YYYY-MM-DD') : null;
      var endDate = vm.endDate ? moment(vm.endDate).format('YYYY-MM-DD') : null;

      Restangular.one('busquedas').customGET('', { from: startDate, to: endDate }).then(function(results) {
        vm.isLoading = true;
        vm.productos = results || [];
        vm.haveResults = vm.productos.length > 0;

        var pieChart = AmCharts.makeChart(id, {
          type: 'pie',
          startDuration: 0,
          theme: 'blur',
          addClassNames: true,
          color: layoutColors.defaultText,
          labelTickColor: layoutColors.borderDark,
          legend: {
            position: 'right',
            marginRight: 100,
            autoMargins: false,
          },
          innerRadius: '40%',
          defs: {
            filter: [
              {
                id: 'shadow',
                width: '200%',
                height: '200%',
                feOffset: {
                  result: 'offOut',
                  in: 'SourceAlpha',
                  dx: 0,
                  dy: 0
                },
                feGaussianBlur: {
                  result: 'blurOut',
                  in: 'offOut',
                  stdDeviation: 5
                },
                feBlend: {
                  in: 'SourceGraphic',
                  in2: 'blurOut',
                  mode: 'normal'
                }
              }
            ]
          },
          dataProvider: (results || []).slice(0, 10),
          valueField: 'contador',
          titleField: 'palabra',
          export: {
            enabled: true
          },
          creditsPosition: 'bottom-left',
    
          autoMargins: false,
          marginTop: 10,
          alpha: 0.8,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
          pullOutRadius: 0,
          pathToImages: layoutPaths.images.amChart,
          responsive: {
            enabled: true,
            rules: [
              // at 900px wide, we hide legend
              {
                maxWidth: 900,
                overrides: {
                  legend: {
                    enabled: false
                  }
                }
              },
    
              // at 200 px we hide value axis labels altogether
              {
                maxWidth: 200,
                overrides: {
                  valueAxes: {
                    labelsEnabled: false
                  },
                  marginTop: 30,
                  marginBottom: 30,
                  marginLeft: 30,
                  marginRight: 30
                }
              }
            ]
          }
        });
    
        pieChart.addListener('init', handleInit);
    
        pieChart.addListener('rollOverSlice', function (e) {
          handleRollOver(e);
        });
    
        function handleInit() {
          pieChart.legend.addListener('rollOverItem', handleRollOver);
        }
    
        function handleRollOver(e) {
          var wedge = e.dataItem.wedge.node;
          wedge.parentNode.appendChild(wedge);
        }
      });
    };
  }
})();

